<template>
  <div id="app">
    <div id="container">
      <h1>Crea una nueva tarea</h1>
      <p>
        <b>Tarea</b> <input type="text" v-model="tarea" placeholder='Ingrese nueva tarea'>
        <button @click="crearTarea()">Crear</button>
      </p>
      <h1>Lista</h1>
      <ol>
        <li v-for="tarea in tareas">{{tarea.nombre}}</li>
      </ol>
    </div>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'

export default {
  name: 'App',
  components: {
    HelloWorld
  },
  data(){
    return{
      tarea:'',
      tareas: []
    };
  },
  methods:{
    crearTarea(){
      const {tarea} = this
      this.tareas.push({"nombre":tarea});
      this.tarea = '';
    }
  }
}
</script>

